.black-bg {
    position: fixed;
    inset: 0;
    margin: 0;
    background-color: rgba(0, 0, 0, 0.234);
    z-index: 5;
    cursor: default;
}

.quick-editor {
    position: fixed;
    // height: 300px;
    // width: 300px;
    z-index: 5;
    // display: flex;
    display: grid;
}

.quick-editor-textarea {
    position: fixed;
    width: 256px;
    box-shadow: rgba(9, 30, 66, .251);
    border-radius: 3px;

    .quick-editor-img {
        display: flex;
        align-items: center;
        border-radius: 3px;

        img {
            border-radius: 3px;
            height: 100%;
        }
    }

    form {
        // position: fixed;
        background-color: #fff;
        z-index: 3;
        left: 0;
        top: 0;
    }

    textarea {
        background-color: #fff;
        border: none;
        border-radius: 3px;
        min-height: 60px;
        padding: 9px 8px;
        width: 256px;
        font-size: 14px;
        overflow-y: visible;
        overflow-x: hidden;

        &::selection {
            color: black;
            background-color: rgba(255, 201, 102, 0.406);
        }
    }

    .quick-editor-card-details {
        // position: absolute;
        bottom: 0;
        text-decoration: none;
        align-items: center;
        padding: 0 10px;
        margin: 0 4px 4px 0;
        width: 256px;
        display: grid;
        grid-template-columns: 1fr 1fr;

        .preview-details-members {
            grid-column: 2 / 3;
            justify-self: end;

            span {
                margin-right: 4px;
            }

            .member-img {
                height: 28px;
                width: 28px;
                margin-right: -3px;
                object-fit: cover;
                border-radius: 25em;
            }
        }

        .left {
            grid-column: 1 / 2;
            grid-row: 1 / 1;
            display: flex;
            gap: 10px;
            align-items: baseline;
            color: #5e6c84;

            .preview-details-checklist {
                display: flex;
                gap: 5px;
                padding-bottom: 3px;
                width: 50px;
                border-radius: 3px;
                align-items: center;
                justify-content: center;
                padding: 0 2px;
                margin: 0 4px 4px 0;
                font-size: 12px;
                line-height: 20px;

                .preview-details-checklist-icon {
                    margin-top: 3px;
                    font-size: 15px;
                }
            }

            .attach-container {
                // grid-column: 2 / 3;
                margin-inline-end: 8px;
                display: flex;
                align-items: center;
                gap: 5px;
                align-self: end;
                margin: 0 4px 4px 0;

                .preview-details-attach-icon {
                    padding-top: 5px;
                    color: #5e6c84;
                    font-size: 12px;
                }
            }



            .preview-details-desc-icon {
                // grid-column: 2 / 3;
                padding-top: 5px;
                font-size: 15px;
            }

        }


        // .preview-details-checklist {
        //     grid-column: 1 / 2;
        //     font-size: 12px;
        //     color: #5e6c84;
        //     display: flex;
        //     align-items: center;
        //     gap: 6px;
        //     width: 50px;
        //     border-radius: 3px;
        //     align-items: center;
        //     justify-content: center;
        //     padding: 2px;

        //     .preview-details-checklist-icon {
        //         font-size: 15px;
        //         padding-top: 4px;
        //     }
        // }
    }

    .save-btn {
        background-color: $focus-input;
        color: #fff;
        // margin-right: 4px;
        margin-top: 10px;
        margin-left: 0;
        height: 32px;
        border-radius: 3px;
        padding: 6px 12px;
        font-size: 14px;
        width: 82px;
        // position: absolute;
        top: 150px;
        left: 0;
    }

}

// .quick-editor-btns.fade-in {
//     opacity: 1;
//     transform: translateX(0);
// }

.quick-editor-btns {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    justify-content: center;
    position: fixed;
    // top: 0;
    z-index: 2;
    width: max-content;
    -webkit-transition: opacity 85ms ease-in, transform 85ms ease-in;
    -moz-transition: opacity 85ms ease-in, transform 85ms ease-in;
    -ms-transition: opacity 85ms ease-in, transform 85ms ease-in;
    -o-transition: opacity 85ms ease-in, transform 85ms ease-in;
    transition: opacity 85ms ease-in, transform 85ms ease-in;

    button {
        background-color: rgba(0, 0, 0, .6);
        color: #fff;
        display: flex;
        align-items: center;
        gap: 10px;
        font-size: 15px;
        height: 32px;
        margin: 0 0 4px 8px;
        padding: 6px 12px 6px 12px;
        border-radius: 3px;
        cursor: pointer;
        // transition: .3s;
        -webkit-transition: margin 85ms ease-in;
        -moz-transition: margin 85ms ease-in;
        -ms-transition: margin 85ms ease-in;
        -o-transition: margin 85ms ease-in;
        transition: margin 85ms ease-in;

        .quick-icon {
            font-size: 14px;
            padding-top: 3px;
        }


        &:hover {
            margin-inline-start: 15px;
            background-color: rgba(0, 0, 0, .9);
        }
    }
}

.quick-editor .extras-menu {
    z-index: 3;
    left: 266px;
    top: 135px;

    .title-container {

        .close-btn {

            background-color: rgba(0, 0, 0, 0);
            border-radius: 50%;
            color: #42526e;
            height: 32px;
            margin: 4px;
            overflow: hidden;
            padding: 4px;
            position: absolute;
            right: 0;
            top: 0;
            width: 32px;
            line-height: 32px;
            z-index: 1;
        }
    }
}