*, *::before, *::after {
    box-sizing: border-box;
}

::selection {
    background-color: $clr1;
    color: $clr6;
}

html {
    font-family: montserrat;
    line-height: 1.6;
    color: $clr2;
}

body {
    font-size: rem(14px);
    margin: 0;
    font-family: system-ui,'Segoe UI', 'roboto', 'ubuntu', 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: $clr11;
}

h1, h2, h3, h4, h5, h6 {
    font-family: system-ui,'Segoe UI', 'roboto', 'ubuntu', 'Open Sans', 'Helvetica Neue', sans-serif;
    
    margin: 0 0 0.6em;
    line-height: 1;
}

img {
    width: 100%;
}

button {
    background-color: transparent;
    font-family: system-ui,'Segoe UI', 'roboto', 'ubuntu', 'Open Sans', 'Helvetica Neue', sans-serif;
}

textarea {
    font-family: system-ui,'Segoe UI', 'roboto', 'ubuntu', 'Open Sans', 'Helvetica Neue', sans-serif;
    outline: none;
    overflow: auto;
    resize: none;

}

p {
    margin: 0 0 1em;
}

a {
    text-decoration: none;
}
a:hover {
    text-decoration: underline;
}

#root {
    min-height: 100vh;
}

.required {
    color: $clr13;
}

.fa, .fa-brands, .fa-regular {
    font-style: normal;
}

.placeholder {
    background-color: rgba(0, 0, 0, 0.25);
}

main {
    > * {
        padding-top: rem(44px);
    }
}

.hidden {
    opacity: 0;
    width: 0;
    height: 0;
    pointer-events: none;
}

::-webkit-scrollbar {
    width: 0px;
    height: 13px;
    background-color: #5858585a;
    border-radius: 43px;
}

::-webkit-scrollbar-track {
    border-radius: 0px;
}

::-webkit-scrollbar-thumb {
    background-color: #c5c8d07f;
    border-radius: 4px;
    border: 0px solid transparent;
}