.member-img {
    height: 32px;
    margin-right: -3px;
    object-fit: cover;
    width: 32px;
    border-radius: 25em;
    border: .1px solid white;

    img {
        border-radius: 25em;
    }
}

.board-header {
    display: flex;
    flex-wrap: wrap;
    background-image: linear-gradient(180deg, #00000040, transparent);
    max-width: 100%;
    justify-content: space-between;
    align-items: center;
    padding: 12px 4px 0px 12px;
    position: relative;
    min-height: 44px;
    flex-wrap: wrap;


    .board-header-btn-icon {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 6px;
        margin: 0;
        font-size: 14px;
        border-radius: 3px;
        background-color: rgba(255, 255, 255, 0.2);
        padding: 6px 12px;
        height: 29px;


        &:hover {
            background-color: $btn-hover;
        }

    }


    .left {
        display: flex;
        margin-bottom: 5px;
        align-items: flex-start;
        gap: 2px;
        flex-wrap: wrap;


        span.line {
            border-left: 1px solid #ffffff29;
            float: left;
            height: 16px;
            margin: 8px 0px 5px 10px;
        }

        .star-icon-box {
            display: flex;
            align-items: center;
            justify-content: center;
            height: 20px;
            width: 20px;
            border-radius: 3px;
            padding: 16px 16px 14px;
            background-color: rgba(255, 255, 255, 0.2);
        }

        .board-header-star-btn {
            color: #f2d600;
            font-size: 16px;
        }

        .board-header-text.edit-mode {
            display: none;
        }

        .board-header-text {
            font-size: 18px;
            font-weight: 700;
            line-height: 32px;
            height: 32px;
            padding: 0 12px;
            color: whitesmoke;
            margin: 0px 0 6px 4px;
            cursor: pointer;
            // letter-spacing: 1px;

            &:hover {
                background-color: $btn-hover;
                border-radius: 3px;
            }
        }


        .board-name-input {
            display: none;
        }

        .board-name-input.edit-mode {
            background-color: rgba(255, 255, 255);
            border: 0;
            display: block;
            width: 93px;
            border-radius: 2px;
            font-size: 18px;
            font-weight: 700;
            height: 32px;
            margin: 0px 2px 6px 4px;
            padding: 0 12px;

            &:focus {
                box-shadow: inset 0 0 0 2px $focus-input;
            }
        }

        .board-members {
            align-items: center;
            display: flex;
            justify-content: center;
            margin-right: 11px;
            margin-left: 10px;

            .member-img {
                height: 32px;
                margin-right: -3px;
                object-fit: cover;
                width: 32px;
                border-radius: 25em;
                border: .1px solid #ffffff5e;

                img {
                    border-radius: 25em;
                    height: 100%;
                    object-fit: cover;
                }
            }
        }

        .invite-icon {
            margin-inline-end: 5px;
        }



        @include for-narrow-layout {
            margin-bottom: 2px;
        }
    }

    .right {
        display: flex;
        margin-bottom: 11px;
        align-items: flex-start;
        gap: 5px;
        flex-wrap: wrap;

        span.line {
            border-left: 1px solid #ffffff29;
            float: left;
            height: 16px;
            margin: 8px 8px 5px 4px
        }

        button.menu {
            padding: 6px;
            width: 29px;
            height: 29px;
        }

        // display: flex;
        // margin: 0 4px 5px auto;
        // align-items: flex-start;
        // padding-inline-start: 5px;
        // -webkit-transition: margin .12s ease-in;
        // -moz-transition: margin .12s ease-in;
        // -ms-transition: margin .12s ease-in;
        // -o-transition: margin .12s ease-in;
        // transition: margin .12s ease-in;
        // gap: 10px;

    }

}

.extras-menu {
    flex-direction: column;
    background-color: white;
    color: #172b4d;
    position: fixed;
    top: 55px;
    left: 5px;
    min-height: 100px;
    min-width: 270px;
    border-radius: 3px;
    z-index: 2;
    box-shadow: 0 8px 16px -4px rgb(9 30 66 / 25%), 0 0 0 1px rgb(9 30 66 / 8%);

    .title-container {
        color: #5e6c84;
        line-height: 20px;
        text-align: center;
        border-bottom: 1px solid rgba(9, 30, 66, 0.129);
        margin: 0 12px 8px;
        padding-top: 10px;

        .close-btn {
            background-color: rgba(0, 0, 0, 0);
            border-radius: 50%;
            color: #42526e;
            height: 32px;
            margin: 4px;
            overflow: hidden;
            padding: 4px;
            position: absolute;
            right: 0;
            top: 0;
            width: 32px;
            line-height: 32px;
            z-index: 1;
        }
    }

    .member-selector {
        overflow-y: scroll;
        max-height: 40vh;
    }
}